<template>
  <transition name="translate">
    <div class="book-sort" @scroll="downloadMore" ref="bookSort">
      <router-view></router-view>
      <back></back>
      <i class="icon-huidingbu" v-show="isLong" @click="toTop"></i>
      <ul>
        <li class="book-sort-select">
          <span @click="selectGender(0)" :class="{active:gender==0}">全部</span>
          <span @click="selectGender(1)" :class="{active:gender==1}">男频</span>
          <span @click="selectGender(2)" :class="{active:gender==2}">女频</span>
        </li>
        <li class="book-sort-select">
          <span v-for="type in types" @click="selectType(type.id)"
                :class="{active:type.id==classId}">{{type.name}}</span>
        </li>
        <li class="book-sort-select">
          <span @click="selectEnd(0)" :class="{active:end==0}">全部</span>
          <span @click="selectEnd(1)" :class="{active:end==1}">连载</span>
          <span @click="selectEnd(2)" :class="{active:end==2}">完结</span>
        </li>
        <li class="book-sort-select">
          <span @click="selectChargeType(0)" :class="{active:chargeType==0}">全部</span>
          <span @click="selectChargeType(1)" :class="{active:chargeType==1}">免费</span>
          <span @click="selectChargeType(2)" :class="{active:chargeType==2}">收费</span>
        </li>
      </ul>
      <book-list :datas="books"></book-list>
      <loading :isLoading="isLoading"></loading>

    </div>
  </transition>


</template>

<script>
  import Loading from "components/base/Loading"
  import BookList from "components/base/BookList"
  import Back from "components/base/Back"
  import {getSort} from "api/bookshop"
  import {getBookList} from "api/booksort"
  import {getBooks2} from "common/js/class"
  import {ERR_OK, ERR_EMPTY} from "common/js/config"
  import {mapGetters, mapMutations} from "vuex"

  export default {
    name: 'book-sort',
    components: {
      Loading,
      BookList,
      Back,
    },
    created() {
    },
    mounted(){
      this._getSort();

      this._getBookList();
    },
    computed: {
      ...mapGetters([
        "classId",
        "gender",
        "end",
        "page",
        "chargeType",
        "keyword"
      ])
    },
    methods: {
      toTop() {
        let totalL = this.$refs.bookSort.scrollTop;
        let timer = setInterval(() => {
          if (totalL - 40 < 0) {
            totalL = 0;
            clearInterval(timer)
          } else {
            totalL -= 40;
          }
          this.$refs.bookSort.scrollTo(0, totalL);

        }, 20)
      },
      downloadMore() {
        if (this.on) {
          let el = this.$refs.bookSort,
            h = el.offsetHeight,
            scrollHeight = el.scrollHeight,
            scrollTop = el.scrollTop;
          if (h <= scrollTop) {
            this.isLong = true;
          } else {
            this.isLong = false;
          }
          if (h + scrollTop >= scrollHeight) {
            this.on = false;
            this.set_page(this.page + 1)
            this._getBookList();
            this.timer = setTimeout(() => {
              clearTimeout(this.timer);
              this.on = true;
            }, 3000)
          }
        }
      },
      selectGender(id) {
        this.set_gender(id);
        this.set_page(1);
        this._getBookList();
      },
      selectType(id) {
        this.set_classId(id);
        this.set_page(1);
        this._getBookList();
      },
      selectEnd(num) {
        this.set_end(num);
        this.set_page(1);
        this._getBookList();
      },
      selectChargeType(num) {
        this.set_chargeType(num);
        this.set_page(1);
        this._getBookList();
      },
      _getSort() {
        getSort().then(res => {
          if (res.status == ERR_OK) {
            let data = res.data.show.module6.class_data;
            for (let key in data) {
              if (data.hasOwnProperty(key)) {
                let ary = [];
                ary.id = key;
                ary.name = data[key]["name"];
                ary.num = data[key]["num"];
                this.types.push(ary);
                ary = null;
              }
            }
          }
        })
      },
      _getBookList() {
        getBookList(this.page, this.pageSize, this.classId, this.gender, this.end, this.chargeType, this.keyword).then(res => {
            if (res.status == ERR_OK) {
              if (res.data.state == ERR_EMPTY) {
                this.isLoading = 2;
                this.books = [];
                return;
              }
            }
            if (this.page == 1) {
              this.books = [];
            }
            let data = res.data.data;
            this.isLoading = this.page * this.pageSize >= res.data.total ? 1 : 0;
          data.forEach(item => {
              this.books.push(getBooks2(item))
            })
          }
        )
      },
      ...mapMutations({
        "set_classId": "SET_CLASSID",
        "set_end": "SET_END",
        "set_chargeType": "SET_CHARGETYPE",
        "set_gender": "SET_GENDER",
        "set_page": "SET_PAGE"
      })
    },
    data() {
      return {
        types: [{id: "", name: "全部", num: 0}],
        bookList: [],
        pageSize: 18,
        books: [],
        isLong: false,
        timer: null,
        on: true,
        isLoading: 0,
      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .book-sort {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    z-index: 5555;
    font-size: @font-size-l;
    background-color: @color-bg-white;
    overflow: auto;

    .icon-huidingbu {
      position: fixed;
      font-size: .5rem;
      color: @color-theme;
      cursor: pointer;
      z-index: 5;
      bottom: .3rem;
      right: .3rem;

    }
    .book-sort-select {
      position: relative;
      padding: .15rem;
      overflow: hidden;
      &::after {
        content: "";
        bottom: 0;
        right: 0;
        left: 0;
        background: @color-bg-gray;
        position: absolute;
        height: 1px;
        border-bottom: 1px solid @color-font-gray;
        transform: scaleY(.5);
      }
      span {
        float: left;
        font-size: @font-size-m;
        line-height: 1.7;
        padding: 0 .15rem;
        margin-right: .15rem;
        margin-bottom: .15rem;
        border: 1px solid @color-font-gray;
        border-radius: .4rem;
        cursor: pointer;
        &.active {
          color: @color-theme;
        }
      }
    }
  }

  .translate-enter-active, .translate-fade-active {
    transition: all .7s
  }

  .translate-enter, .translate-leave-to {
    transform: translateX(100%);
  }
</style>



