<template>
  <swiper :options="options" :class="newClass">
    <!-- slides -->
    <swiper-slide v-for="slider in sliders" :key="slider.id">
      <div class="swiper-slide-href" @click="selectBookId(slider.id)">
        <div class="swiper-slide-wrap">
          <img :src="slider.cover" class="swiper-slide-href-img">
        </div>
        <h3 v-if="needIntro" class="swiper-slide-title">{{slider.book}}</h3>
        <p v-if="needIntro" class="swiper-slide-author">{{slider.author}}</p>
      </div>

    </swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination" v-if="options.pagination"></div>
  </swiper>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import {mapMutations} from "vuex"

  export default {
    name: 'swi',
    components: {
      swiper,
      swiperSlide
    },
    methods: {
      selectBookId(id) {
        this.set_bookId(id);
        this.$router.push({path: `/bookintro/${id}`});
      },
      ...mapMutations({
        "set_bookId": "SET_BOOKID"
      })
    },
    props: {
      needIntro: {
        type: Boolean,
        default: false,
      },
      newClass: {
        type: String,
        default: ""
      },
      sliders: {
        type: Array,
        default() {
          return [{cover: ""}]
        }
      },
      options: {
        type: Object,
        default() {
          return {
            lazy: false,
            autoplay: false,
            direction: "horizontal",
            loop: true,
            effet: "slide",
            slidesPerView: 1,
            coverflowEffect:{
              rotate:0,
              stretch:10,
              depth:60,
              modifier:2,
              slideShadows:true
            },
            spaceBetween: 0,
            pagination: {
              el: ".sweper-pagination"
            }
          }
        }
      }
    },
    data() {
      return {}
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .swiper-slide {
    cursor: pointer;
    .swiper-slide-href {
      display: block;
      text-align: center;
      line-height: 1.5;
      .swiper-slide-wrap {
        background: url("~common/img/logo.png");
        .swiper-slide-href-img {
          display: block;
          width: 100%;
        }
        .swiper-slide-title {
          font-size: @font-size-m;
          .nowrap;

        }
        .swiper-slide-author {
          font-size: @font-size-s;
          .nowrap;
        }
      }
    }
  }

  .book-shop-top-sliders {

  }

  .panel-content {
    .swiper-slide-title {
      font-size: @font-size-m;
      .nowrap;
    }
    .swiper-slide-author {
      font-size: @font-size-s;
      .nowrap;
    }
  }

  /*.book-shop-top-sliders{*/
  /*.swiper-slide{*/
  /*width:auto;*/
  /*.swiper-slide-href{*/
  /*.swiper-slide-href-img{*/
  /*display: block;*/
  /*}*/
  /*}*/

  /*}*/
  /*}*/
</style>



