<template>
  <div class="shelf">
    <div class="shelf-top">
      <img  v-if="books[0]" v-lazy="books[0].cover" class="newest-book-img">
      <h3 class="newest-book-title" v-if="books[0]">{{books[0].title}}</h3>
      <span class="newest-book-action" v-if="books[0]">继续阅读</span>
    </div>
    <ul class="book-list">
      <li v-for="book in books" class="book-list-item" @click="toReader(book.id,book.cid)">
        <div class="book-display">
          <img v-lazy="book.cover" class="book-list-item-img">

        </div>
        <h4 class="book-list-item-title">{{book.title}}</h4>
      </li>
      <li class="book-list-item book-list-item-display" @click="toShop">
        <i class="icon-jia"></i>
      </li>
    </ul>
  </div>
</template>

<script>
  import {getBooks} from "common/js/storage"
  import {mapMutations,mapGetters} from "vuex"

  export default {
    name: 'shelf',
    created() {
      this._getBooks();
    },
    methods: {
      toReader(bookid,chapid){
        this.set_bookid(bookid);
        this.set_chapid(chapid);
        this.$router.push({path:`/bookreader/${chapid}`})
      },
      toShop(){
        this.$router.push({path:"/bookshop"})
      },
      _getBooks() {
        let ary = getBooks();
        if(ary){
          ary.sort((a, b) =>{return b.time - a.time})
          this.books = ary;
          return
        }
        this.books=[{cover:"",title:"",cid:""}]

      },

      ...mapMutations({
        "set_bookid":"SET_BOOKID",
        "set_chapid":"SET_CHAPID",
      })
    },
    watch:{
      bookId(neww,old){
        this._getBooks();
      }
    },
    computed:{
      ...mapGetters([
        "bookId"
      ])
    },
    data() {
      return {
        books: [],
      }
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .shelf {
    font-size: @font-size-s;
    line-height: 2;
    overflow: hidden;
    margin-bottom: .6rem;
    .shelf-top {
      height: 3rem;
      background: url("~common/img/shelf.jpg") no-repeat;
      background-size: cover;
      padding-left: 2.5rem;
      position: relative;
      color:@color-font-white;
      .newest-book-img{
        position: absolute;
        left: .5rem;
        top: .4rem;
        width: 1.8rem;
      }
      .newest-book-title{
        font-size: @font-size-m;
        padding-top: .5rem;
      }
      .newest-book-action{
        font-size: @font-size-xs;
        cursor: pointer;
      }
    }
    .book-list{
      padding:.15rem;
      margin-left:-3%;
      margin-bottom: .6rem;
      .book-list-item{
        width: 30%;
        position: relative;
        float: left;
        margin-bottom: .2rem;
        margin-left: 3%;
        .book-display{
          height: 2.6rem;
          overflow: hidden;
          &:hover{
            .book-list-item-img{
              transform: scale(1.3);
            }
          }
          .book-list-item-img{
            width: 100%;
            display: block;
            top: 0;
            left: 0;
          }
        }

        .book-list-item-title{
          text-align: center;
          .nowrap;
        }
        &.book-list-item-display{
          background-color: rgba(0,0,0,.3);
          height: 2.6rem;
          position: relative;
          .icon-jia{
            width: 0.2rem;
            height: 0.2rem;
            font-size: @font-size-l;
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom:0;
            margin: auto;
            &::before{
              position: absolute;
              left: 0;
              top: 0;
            }
          }
        }
      }
    }

  }
</style>



