import {bookKey} from "./config";
import {getTime} from "../common/js/js";
import axios from "axios"
import md5 from "js-md5";
export function getChap(id,chapid) {
  let url="https://ognv1.sqreader.com/index.php?r=pcapi/pcbook/chaptercontent",
    data=new FormData,
    time=getTime();
  data.append("bid",id);
  data.append("chapid",chapid);
  data.append("timestamp",time);
  data.append("sign",md5(id+chapid+time+bookKey));
  return axios.post(url,data);
}



